.gradient-bg {
  background-image: linear-gradient(180deg, #5869FF 0%, #3B3960 54.69%, #0A090B 100%);
  background-repeat: no-repeat;
  height: 3000px;
}

.section {
  margin-top: 20px;
  margin-bottom: 20px;
  height: 80vh;
  max-height: 80vh;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.section + .section {
  margin-top: 10vh;
}

.stars {
  width: 70%;
}
.stars:hover {
  animation: pulse;
  animation-duration: 0.5s;
}

button.btn:hover {
  animation: pulse;
  animation-duration: 1s;
}

#rocket {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
}

#spaceship {
  --size: 64px;
  --offset-distance: 0%;
  max-height: var(--size);
  max-width: var(--size);
  position: absolute;
  transform: rotateZ(90deg);
  z-index: inherit;
  /* offset-path: path(
    "M269.5 2.99999C269.5 2.99999 195.843 123.363 157 237C82.0609 456.237 453.483 723.581 449 991.5C443.808 1301.79 36.5035 1272.23 4.99999 1549.5C-18.7093 1758.17 200.5 1835 294 2196C326 2530 294 2708.5 294 2708.5"
  ); */
  /*   offset-rotate: 90deg; */
  /*  offset-rotate property can also be animated since it is a number  */
  offset-distance: var(--offset-distance);
}

@keyframes move {
  100% {
    offset-distance: 100%;
  }
}
#path {
  height: auto;
}
#path svg {
  width: 80%;
}

.space-trail {
  position: absolute;
  top: 0;
  left: 0;
  width: fit-content;
  height: inherit;
  z-index: 0;
}

* {
  position: relative;
  z-index: auto;
}

